:root {
  font-size: 1em;
}
* {
  box-sizing: border-box;
}
ul {
  padding: 0;
  margin: 0;
  list-style-type: none;
}
body {
  margin: 0;
  background: #e1e1e1;
  font-family: 'Overpass', sans-serif;
}
h1 {
  font-size: 1.4rem;
}
h2 {
  font-size: 1.3rem;
  font-weight: 400;
}
h3 {
  font-size: 1.25rem;
}
h4 {
  font-size: 1.1rem;
}
h5 {
  font-size: 1.0rem;
}
.u-textCenter {
  text-align: center!important;
}
.u-bgPos--centerCenter {
  background-position: 50% 50%;
}
