:root {
  --transitionBase: cubic-bezier(0.215, 0.61, 0.355, 1);
  --timeXShort: 100ms;
  --timeShort: 200ms;
  --timeMedium: 300ms;
  --timeLong: 400ms;
  --timeXLong: 500ms;
  --timeXLLong: 800ms;
  --cardWidth: 70%;
}
.CardListingWrapper {
  overflow: hidden;
  z-index: 5;
  position: relative;
  transition: top var(--timeXLLong) var(--transitionBase), opacity var(--timeXLLong) ease, visibility var(--timeXLLong) ease;
  opacity: 1;
  visibility: visible;
}
.CardListingWrapper.is-hiding {
  opacity: 0;
  visibility: hidden;
}
.CardListingWrapper .CardListing {
  height: 70vh;
  max-height: 400px;
  margin-top: 1em;
  margin-bottom: 1em;
  display: flex;
  margin-left: 2%;
}
.CardListingWrapper .CardListing.is-animating {
  transition: transform 200ms cubic-bezier(0.5, 0, 0.5, 1);
}
@keyframes cardListingWrapper {
  from {
    height: 70vh;
  }
  to {
    height: 37vh;
  }
}
@keyframes fadeOut {
  from {
    opacity: 1;
  }
  to {
    opacity: 0;
  }
}
#Card-detail {
  visibility: hidden;
  opacity: 0;
  top: 27vh;
  left: 3%;
  position: absolute;
  width: 100%;
  background: #fff;
  transform: scale(0.7);
  padding: 1rem;
  transition: all 333ms ease;
  transform-origin: left top;
  overflow: hidden;
  transform: translateZ(0);
  will-change: transform;
}
#Card-detail.is-active {
  visibility: visible;
  top: 37vh;
  left: 0;
  opacity: 1;
  transform: scale(1);
}
.Card {
  border: solid 4px #efefef;
  border-radius: 8px;
  box-shadow: 0 0 5px #b5b5b5;
  background-color: #ccc;
  height: 60vh;
  max-height: 400px;
  width: var(--cardWidth);
  position: relative;
  background-size: cover;
  transition: height var(--timeXLong) var(--transitionBase), top 333ms ease, left 333ms ease, width 333ms ease;
  margin: 0 1%;
  text-align: center;
  float: left;
}
.Card h2 {
  transform: translateX(-50%);
  left: 50%;
  position: absolute;
  bottom: 0;
  color: rgba(255, 255, 255, 0.6);
  width: 100%;
}
.Card.is-fixed {
  position: fixed;
  width: 65.8%;
  z-index: 10;
  margin: 0;
  box-shadow: 0 0 0 0;
}
.Card.is-top {
  position: fixed;
  top: 0!important;
  left: 0!important;
  width: 100%;
  height: 37vh;
  margin: 0;
  border: 0;
}
.Card.is-top.is-now-abs {
  position: absolute;
}
.Card.is-going-back {
  animation: fadeOut 300ms forwards;
}
.Card.is-active {
  width: 100%;
  height: 37vh;
  box-shadow: none;
  text-align: left;
  border: 0;
  margin: 0 auto;
}
.Card.is-active h2 {
  transform: translateX(0%);
  left: 2rem;
  color: #ffffff;
}
.Card--footer {
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  background: rgba(100, 100, 100, 0.5);
  height: 62px;
}
.Button--close {
  position: fixed;
  top: 0;
  right: 0;
  z-index: 15;
}
